<template>
    <div class="tableWrap">
        <div class="table-header flex flex-wrap">
            <div class="table-item" :class="getClass(item.key)"  v-for="(item, index) in columns" :key="index">
                {{item.title}}
            </div>
        </div>
        <div class="table-row flex flex-wrap" v-for="(item, index) in data" :key="index">
            <div class="table-item" :class="getClass(ele.key)" v-for="(ele, eleIndex) in columns" :key="eleIndex">
                <!-- <div v-show="item.edit"> -->
                    <div v-if="ele.key === 'name'">
                        <Input v-if="isManager" v-model="item.name"></Input>
                        <span v-else>{{item.name}}</span>
                    </div>
                    <div v-if="ele.key === 'doorType'">
                        <Select v-if="isManager" transfer v-model="item.doorType"  style="width:85px;">
                            <Option v-for="(ele, eleIndex) in doorTypeList" :value="ele" :key="eleIndex">{{ ele }}</Option>
                        </Select>
                    </div>
                    <div v-if="ele.key === 'length'">
                        <div v-if="!isDoor || !item.lenFixedFlag" style="display:inline-block;">
                            <Select v-if="isManager" transfer v-model="item.lenPara"  style="width:85px;">
                                <Option v-for="ele in nameList" :value="ele.para" :key="ele.para">{{ ele.name }}</Option>
                            </Select>
                            <span v-else>{{ nameMap[item.lenPara]}}</span>
                        </div>
                        <!-- <span v-if="sleeve">+套线宽度</span> -->
                        <InputNumber v-if="isManager" v-model="item.length"></InputNumber>
                        <span v-else>{{item.length >= 0 ? '+' + item.length : item.length}}</span>
                        <span v-if="sleeve">
                            + {{item.name.indexOf('横') > -1 ? 2 : 1}}x套线宽度
                        </span>
                        <i-switch v-if="isDoor && isManager" v-model="item.lenFixedFlag" /></i-switch >
                    </div>
                    <div v-if="ele.key === 'widthWord'">
                        与型号相关
                    </div>
                    <div v-if="ele.key === 'width'">
                        <div v-if="item.showCondition">
                            条件宽度: <InputNumber v-if="isManager" style="width:50px;" v-model="item.width.fixedLen"></InputNumber> 
                            <span v-else>{{item.width.fixedLen}}</span>
                            最大宽度:<InputNumber v-if="isManager" style="width:50px;" v-model="item.width.maxLen"></InputNumber>
                            <span v-else>{{item.width.maxLen}}</span>
                        </div>
                        <div v-else>
                            <span v-show="!item.fixedFlag">{{isDoor ? '门扇宽度' : '门洞厚度'}}</span> 
                            <InputNumber v-if="isManager" style="width:50px;" v-model="item.width"></InputNumber>
                            <span v-else>{{item.width >= 0 ? '+' + item.width : item.width}}</span>
                            <i-switch v-if="isDoor && isManager" v-model="item.fixedFlag" /></i-switch >
                        </div>
                    </div> 
                            
                    <!-- 厚 -->
                    <div v-if="ele.key === 'thick'">
                        <!-- 因为厚度可能是内什么的，所以不能为number -->
                            <Input v-if="isManager" style="width:50px;" v-model="item.thick"></Input> <span v-else>{{item.thick}}</span> mm
                            <span v-if="item.thickType !== undefined">
                                <Select transfer v-if="isManager" v-model="item.thickType">
                                    <Option v-for="ele in typeList" :value="ele" :key="ele">{{ ele }}</Option>
                                </Select>
                                <span v-else>{{item.thickType}}</span>
                            </span>
                            <span v-else>
                                多层板
                            </span>
                    </div>
                    <div v-if="ele.key === 'thickness'">
                        只有E系列才有
                    </div>
                    <div v-if="ele.key === 'num'">
                        <InputNumber v-if="isManager" v-model="item.num"></InputNumber>
                        <span v-else>{{item.num}}</span>
                    </div>
                <!-- </div> -->
                <!-- <span v-show="!item.edit">{{item[ele.key]}}</span> -->
                <div class="action" v-if="ele.key === 'action'">
                    <!-- <Button class="first" type="text" v-show="!item.edit" @click="changeEdit(index, true)">编辑</Button>
                    <Button class="first" type="text" v-show="item.edit" @click="changeEdit(index, false)">保存</Button> -->
                    <Button type="text" @click="deleteItem(index)">删除</Button>
                    <Button type="text" @click="addItem(index)">新增</Button>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    props: {
        list: Array,
        isDoor: Boolean,
        sleeve: Boolean,
        doorName: String
    },
    data() {
        return {
            isManager: common.isManager,
            infoMap: {},
            typeList: ['多层板','中纤板'],
            doorTypeList: ['大门', '小门'],
            columns: [{
                title: '名称',
                key: 'name'
            }, {
                title: '大小门',
                key: 'doorType'
            }, {
                title: '长度',
                key: 'length'
            }, {
                title: '宽度',
                key: 'width'
            },{
                title: '厚度',
                key: 'thick'
            }, {
                title: '数量',
                key: 'num'
            }, {
                title: '操作',
                key: 'action'
            }],
            nameList: [{
                name: '门洞宽度',
                para: 'doorWidth'
            }, {
                name: '门洞高度',
                para: 'doorHeight'
            }, {
                name: '门扇宽度',
                para: 'doorLeafWidth'
            }, {
                name: '门扇高度',
                para: 'doorLeafHeight'
            }, ],
            nameMap: {
                doorWidth: '门洞宽度',
                doorHeight: '门洞高度',
                doorLeafWidth: '门扇宽度',
                doorLeafHeight: '门扇高度',
            }
        }
    },
    computed: {
        data() {
            return this.list
        }
    },
    mounted() {
        // 不是门部件或者 是门部件但是不是管理员
        if(!this.isDoor || !this.isManager) {
            this.columns.splice(this.columns.length - 1, 1)
        }
        if(this.doorName) {
            this.columns = [{
                title: '名称',
                key: 'name'
            }, {
                title: '大小门',
                key: 'doorType'
            },{
                title: '长度',
                key: 'length'
            }, {
                title: '宽度',
                key: 'width'
            },{
                title: '厚度',
                key: 'thick'
            }, {
                title: '数量',
                key: 'num'
            }, {
                title: '操作',
                key: 'action'
            }]
        }
        // 如果是套线的话
        if(this.sleeve) {
            this.columns = [{
                title: '名称',
                key: 'name'
            }, {
                title: '长度',
                key: 'length'
            }, {
                title: '宽度',
                key: 'widthWord'
            }, {
                title: '厚度',
                key: 'thick'
            }, {
                title: '厚度(备注)',
                key: 'thickness'
            }, {
                title: '数量',
                key: 'num'
            }]
        }
    },
    methods: {
        getClass(key) {
            if(!key) return 'flex-1'
            if(key === 'name') return 'fixed-width'
            if(key === 'num') return 'fixed-width'
            if(key === 'thick') return 'fixed-width'
            if(key === 'doorType') {
                if(this.doorName) {
                    return 'fixed-width-door'
                }else {
                    return 'not-show'
                }
            }else {
                if(this.isDoor) {
                    return (key === 'width') ? 'fixed-width' : 'flex-1'
                }else {
                    return 'flex-1'
                }
            }
        },
        deleteItem(index) {
            this.data.splice(index, 1)
        },
        addItem(index, name) {
            let item = {
                name: '名称',
                length: 0,
                lenPara: 'doorLeafHeight',
                width: 0,
                thick: 5,
                num: 1,
                thickType: '多层板',
                fixedFlag: false,
                lenFixedFlag: false
            }
            if(this.doorName) {
                item.doorType = '大门'
            }
            this.data.splice(index + 1, 0, item)
        }
    },
}
</script>

<style lang="scss">

@import 'src/assets/common.scss';
// @import '../assets/flexComp.scss';

</style>
