<template>
    <div class="newCalculation">
        <div class="cardWrap" v-if="algorithm">
            <Breadcrumb style="margin-bottom: 10px;">
                <BreadcrumbItem to="/">首页</BreadcrumbItem>
                <BreadcrumbItem> <span v-if="isManager">{{id ? '编辑' : '新建'}}</span>算法信息</BreadcrumbItem>
            </Breadcrumb>
            <Card>
                <div slot="title">
                    基本信息
                    <Button v-if="isManager" class="save-btn" type="primary" @click="save">保存算法</Button>
                </div>
                <Form :model="algorithm.baseInfo" :label-width="80">
                    <FormItem class="one-four" label="名称">
                        <Input v-if="isManager" v-model="algorithm.baseInfo.name" placeholder="请输入名称"></Input>
                        <span v-else >{{algorithm.baseInfo.name}}</span>
                    </FormItem>
                    <FormItem class="one-four" label="分组">
                        <Select v-if="isManager" v-model="algorithm.baseInfo.groupId">
                            <Option v-for="item in groupList" :value="item.objectId" :key="item.title">{{ item.title }}</Option>
                        </Select>
                        <span v-else>{{groupMap[algorithm.baseInfo.groupId]}}</span>
                    </FormItem>
                    <FormItem class="one-four" label="简介">
                        <Input v-if="isManager" v-model="algorithm.baseInfo.info" placeholder="请输入简介"></Input>
                        <span v-else >{{algorithm.baseInfo.info}}</span>
                    </FormItem>
                    <FormItem class="one-four" label="门扇宽度 =">
                        门洞宽度 <InputNumber v-if="isManager" class="new-input" v-model="algorithm.baseInfo.doorLeafWidth" placeholder="请输入门扇宽度"></InputNumber>
                        <span v-else >{{algorithm.baseInfo.doorLeafWidth > 0 ? '+' + algorithm.baseInfo.doorLeafWidth : algorithm.baseInfo.doorLeafWidth}}</span>
                    </FormItem>
                    <FormItem class="one-four" label="门扇高度 =">
                        门洞高度 <InputNumber v-if="isManager" class="new-input" v-model="algorithm.baseInfo.doorLeafHeight" placeholder="请输入门扇高度"></InputNumber>
                        <span v-else >{{algorithm.baseInfo.doorLeafHeight > 0 ? '+' + algorithm.baseInfo.doorLeafHeight : algorithm.baseInfo.doorLeafHeight}}</span>
                    </FormItem>
                    <FormItem class="one-four" label="门扇厚度">
                        <InputNumber v-if="isManager" class="new-input" v-model="algorithm.baseInfo.doorLeafThick" placeholder="请输入门扇厚度"></InputNumber>
                        <span v-else >{{algorithm.baseInfo.doorLeafThick > 0 ? '+' + algorithm.baseInfo.doorLeafThick : algorithm.baseInfo.doorLeafThick}}</span>
                    </FormItem>
                    <FormItem v-if="isManager" class="one-four" label="大图">
                        <Upload :action="uploadFileUrl"
                                accept="image/png, image/jpeg, image/jpg"
                                :format="['jpg','jpeg','png']"
                                :before-upload="beforeUpload">
                            <Button @click.native="uploadImgName = 'bigImg'" icon="ios-cloud-upload-outline">上传大图</Button>
                        </Upload>
                    </FormItem>
                    <FormItem v-if="isManager" class="one-four" label="小图">
                        <Upload :action="uploadFileUrl"
                                accept="image/png, image/jpeg, image/jpg"
                                :format="['jpg','jpeg','png']"
                                :before-upload="beforeUpload">
                            <Button @click.native="uploadImgName = 'smallImg'" icon="ios-cloud-upload-outline">上传小图</Button>
                        </Upload>
                    </FormItem>
                    <div></div>
                    <FormItem class="one-four" label="大图">
                        <div class="img-wrap">
                            <img v-if="algorithm.baseInfo.bigImg" :src="url + algorithm.baseInfo.bigImg" alt=""
                                @click="showPicture('bigImg')">
                        </div>
                    </FormItem>
                    <FormItem class="one-four" label="小图">
                        <div class="img-wrap">
                            <img v-if="algorithm.baseInfo.smallImg" :src="url + algorithm.baseInfo.smallImg" alt=""
                                @click="showPicture('smallImg')">
                        </div>
                    </FormItem>
                </Form>
            </Card>
            <Card class="door-card">
                <p slot="title">房门算法信息</p>
                <Button class="add" @click.native="addNew">新增</Button>
                <Tabs v-model="tabValue">
                    <TabPane label="混油" name="soil">
                        <my-table ref="soil" :list="algorithm.other.soil" :doorName="'soil'" :isDoor="true"></my-table>
                    </TabPane>
                    <TabPane label="贴皮" name="skin">
                        <my-table ref="skin" :list="algorithm.other.skin" :doorName="'skin'" :isDoor="true"></my-table>
                    </TabPane>
                </Tabs>
            </Card>
            <Card>
                <p slot="title">
                    门框算法信息
                    <span class="condition-info">{{word}}</span>
                </p>
                    <my-table ref="door" :list="algorithm.door"></my-table>
            </Card>
            <Card>
                <p slot="title">
                    柜门连套算法信息
                    <!-- <span class="condition-info">{{word}}</span> -->
                    </p>
                    <my-table ref="cupboardDoors" :list="algorithm.cupboardDoors"></my-table>
            </Card>
            <Card>
                <p slot="title">
                    对开门连套算法信息
                    <!-- <span class="condition-info">{{word}}</span> -->
                </p>
                    <my-table ref="openDoors" :list="algorithm.openDoors"></my-table>
            </Card>
            <Card>
                <p slot="title">三面窗套算法信息</p>
                    <my-table ref="windowCover" :list="algorithm.windowCover"></my-table>
            </Card>
            <Card>
                <p slot="title">三面哑口套算法信息</p>
                    <my-table ref="pass" :list="algorithm.dumb"></my-table>
            </Card>
            <Card>
                <p slot="title">套线算法信息</p>
                    <div class="title">
                        房门连套套线、三面垭口套套线、对开门套线、柜门连套套线
                    </div>
                    <my-table ref="pass" :list="algorithm.line.normal" :sleeve="true"></my-table>
                    <div class="title">
                        三面窗套套线
                    </div>
                    <my-table ref="pass" :list="algorithm.line.three" :sleeve="true"></my-table>
            </Card>
        </div>
        <Modal
            v-model="showPictureFlag"
            title="查看大图">
            <div class="big-picture">
                <img v-if="imgUrl" :src="imgUrl">
            </div>
        </Modal>
    </div>
</template>

<script>
import MyTable from '@/components/MyTable.vue'
import backData from '@/assets/data.js'
export default {
    data() {
        return {
            id: '',
            tabValue: 'soil',
            groupList: [],
            url: '',
            showPictureFlag: false,
            imgUrl: '',
            algorithm: null,
            wordMap: {
                name: '填写名称',
                groupId: '选择分组',
                // doorLeafWidth: '填写门扇宽度算法',
                // doorLeafHeight: '填写门扇高度算法',
                bigImg: '填写大图图片地址',
                smallImg: '填写小图图片地址',
            },
            isManager: common.isManager,
            groupMap: {},
            uploadFileUrl: 'https://api2.bmob.cn/2/files/',
            uploadImgName: '',
            word: '门洞厚度：x, 条件宽度:y, z: 挡条最大宽度; 如果 x >= y，挡条宽度 = z，如果x < y，挡条宽度 = z - ( y - x )'
        }
    },
    components: {
        MyTable
    },
    computed: {
        
    },
    mounted() {
        this.id = this.$route.query.id
        if(!this.id) this.algorithm = this.cloneJson(backData.algorithm)
        this.getGroup()
    },
    methods: {
        initAlgorithm(a) {
            this.algorithm = a
        },
        // 获取分组
        getGroup() {
            common.get('classes/Group').then(res => {
                this.groupList = res.results
                this.groupList.forEach(ele => {
                    this.groupMap[ele.objectId] = ele.title
                })
            })
        },
        cloneJson(obj) {
            return JSON.parse(JSON.stringify(obj));
        },
        // 添加新的一条
        addNew() {
            let len = this.algorithm.other[this.tabValue].length
            this.$refs[this.tabValue].addItem(len, this.tabValue)
        },
        // 显示大图
        showPicture(img) {
            this.imgUrl = this.algorithm.baseInfo[img]
            this.showPictureFlag = true
        },
        // 保存算法
        save() {
            for(let i in this.algorithm.baseInfo) {
                let item = this.algorithm.baseInfo[i]
                // 没有内容并且属于其中一个
                if(!item && this.wordMap[i]) {
                    return this.$Message.success( '请'+ this.wordMap[i] +"!"); 
                }
            }
            let baseInfo =this.algorithm.baseInfo 
            let data = {
                groupId: baseInfo.groupId,
                title: baseInfo.name,
                info: baseInfo.info,
                algorithm: this.algorithm
            }
            let isEdit = (this.$route.path === '/edit')
            if(isEdit) {
                common.update('classes/itemList/' + this.id, data).then(res => {
                    this.$Message.success("算法更新成功!");
                })
            }else {
                common.post('classes/itemList', data).then(res => {
                    this.$Message.success("算法保存成功!");
                    // 不是编辑算法的需要刷新其他数据
                        this.algorithm = this.cloneJson(backData.algorithm)
                })
            }
        },
        beforeUpload(file) {
            this.uploadImg(file)
            return false
        },
        uploadImg(file) {
            var reader = new FileReader();
            let _this = this
            reader.readAsArrayBuffer(file)
            reader.onload = (e) => {
                axios({
                    method: 'post',
                    url: 'https://api2.bmob.cn/2/files/' + file.name,
                    headers: {
                        'X-Bmob-Application-Id': 'd5007b04394aef4cdb48560e3a48c6bb',
                        'X-Bmob-REST-API-Key': '5bf0663149c84d47c3d5c8949e025815',
                        'Content-Type': 'content-Type',
                    },
                    data: reader.result
                }).then(function (res) {
                    if(res.status === 200) {
                        _this.$Message.success('图片上传成功!');
                        _this.algorithm.baseInfo[_this.uploadImgName] = res.data.url
                    } 
                })
            }
        }
    }
}
</script>

<style lang="scss">
@import 'src/assets/common.scss';
.newCalculation {
    .condition-info {
        font-size: 12px;
        font-weight: 500;
        color: #666;
        margin-left: 10px;
    }
    .cardWrap {
        padding: 30px 120px;
        .ivu-card {
            margin-bottom: 30px;
            .title {
                margin-bottom: 10px;
                padding: 5px 10px;
                border-radius: 5px;
                border: 1px solid #eee;
                display: inline-block;
                margin-top: 10px;
                &:first-child {
                    margin-top: 0;
                }
            }
            .save-btn {
                float: right;
                margin-top: -9px;
            }
        }
    }
    .img-wrap {
        width: 80px;
        height: 100px;
        line-height: 100px;
        font-size: 30px;
        text-align: center;
        color: #fff;
        background: #aaa;
        overflow: hidden;
        img {
            max-width: 100%;
            cursor: pointer;
        }
    }
    .door-card {
        position: relative;
        .add {    
            position: absolute;
            left: 120px;
            top: 10px;
            cursor: pointer;
        }
        // .ivu-tabs {
        //     // overflow-y: visible;
        //     .ivu-tabs-content-animated {
        //         transition: none;
        //     }
        // }
    }
}
</style>
